@import "@/assets/variables";

.input-wrapper {
  position: relative;
}

.input {
    background: var(--tertiary-bg);
    border: 1px solid var(--tertiary-accent);
    border-radius: 2px;
    padding: 6px;
    font-size: 14px;
    font-family: "IBM Plex Sans", sans-serif;
    color: var(--text-primary);
    height: 100%;
    width: 100%;

    &_clearable {
      padding-right: 25px;

      @include notebook {
        padding-right: 30px;
      }
    }

    &_error {
      border-color: var(--text-error);
      background-color: var(--bg-error);
    }

    @include notebook {
      font-size: 16px;
    }

    &::placeholder {
        font-family: "IBM Plex Sans", sans-serif;
        color: var(--text-primary);
    }
}

.clear {
  position: absolute;
  top: 1px;
  bottom: 0;
  right: 10px;
  width: fit-content;
  height: fit-content;
  fill: #708090;
  margin: auto;

  @include notebook {
    padding: 10px;
    right: 0;
  }

  svg {
    width: 10px;
    height: 10px;
  }
}
